<template>
  <div class="agreement">
    <!-- <i-button @click="back">返回</i-button> -->
    <span class="back" @click="back"><</span> 
    <div class="top">用户协议</div>
  <div v-html="text" class="text"></div>
  </div>
</template>

<script>
  export default {
    name: 'agreement',

    data() {
      return {
        text: ''
      }
    },
    methods: {
      back() {
        this.$router.go(-1)
      }
    },
    created() {
      this.text = this.$route.params.text
    },
  }
</script>

<style lang="less" scoped>
  .agreement {
    height: 100%;
  }

  .text {
    margin-top: 20px;
    text-indent: 10px;
  }

  .top {
    font-size: 16px;
    text-align: center;
    margin-top: -16px;
    border-bottom: 1px solid #000;
    padding-bottom: 10px;
    font-weight: bold;
  }

  .back {
    position: relative;
    top: 10px;
    left: 20px;
    width: 20%;
    display: block;
    font-size: 20px;
  }
</style>